import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Stack, Menu, MenuItem, IconButton, useMediaQuery } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

import Logo from '../assets/images/Logo.png';

const Nav2 = () => {
  const [isMenuOpen, setMenuOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);

  const smallScreen = useMediaQuery('(max-width:600px)'); // Adjust the breakpoint as needed

  const openMenu = (event) => {
    setMenuOpen(true);
    setAnchorEl(event.currentTarget);
  };

  const closeMenu = () => {
    setMenuOpen(false);
    setAnchorEl(null);
  };

  return (
    <Stack
      direction="row"
      justifyContent="space-around"
      sx={{
        gap: { sm: '123px', xs: '40px' },
        mt: { sm: '32px', xs: '20px' },
        justifyContent: 'none',
      }}
      px="20px"
    >
      <Link to="/">
        <img
          src={Logo}
          alt="logo"
          style={{ width: '48px', height: '48px', margin: '0px 20px' }}
        />
      </Link>

      {/* Dropdown Menu for Small Screens */}
      {smallScreen && (
        <IconButton
          onClick={openMenu}
          sx={{ display: { sm: 'none', xs: 'block' }, ml: 'auto' }}
        >
          <MenuIcon />
        </IconButton>
      )}
      <Menu
        anchorEl={anchorEl}
        open={isMenuOpen}
        onClose={closeMenu}
      >
        <MenuItem onClick={() => { closeMenu(); window.location.href = "/"; }}>
          Home
        </MenuItem>
        {/* Add more menu items as needed */}
      </Menu>
    </Stack>
  );
};

export default Nav2;
