import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Stack, Drawer, List, ListItem, IconButton } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';

import Logo from '../assets/images/Logo.png';

const Navbar = () => {
  const [isDrawerOpen, setDrawerOpen] = useState(false);

  const openDrawer = () => {
    setDrawerOpen(true);
  };

  const closeDrawer = () => {
    setDrawerOpen(false);
  };

  return (
    <Stack direction="row" justifyContent="space-around" sx={{ gap: { sm: '123px', xs: '40px' }, mt: { sm: '32px', xs: '20px' }, justifyContent: 'none' }} px="20px">
      <Link to="/">
        <img src={Logo} alt="logo" style={{ width: '48px', height: '48px', margin: '0px 20px' }} />
      </Link>
      <Stack
        direction="row"
        gap="40px"
        fontFamily="Alegreya"
        fontSize="24px"
        alignItems="flex-end"
        sx={{ display: { sm: 'flex', xs: 'none' } }}
      >
        <Link
          to="/"
          style={{
            textDecoration: 'none',
            color: '#3A1212',
            borderBottom: '3px solid transparent',
            transition: 'border-bottom 0.3s',
          }}
          onMouseOver={(e) => {
            e.target.style.borderBottom = '3px solid #ff004f';
          }}
          onMouseOut={(e) => {
            e.target.style.borderBottom = '3px solid transparent';
          }}
        >
          Home
        </Link>
        <a
          href="#exercises"
          style={{
            textDecoration: 'none',
            color: '#3A1212',
            borderBottom: '3px solid transparent',
            transition: 'border-bottom 0.3s',
            marginRight: '20px',
          }}
          onMouseOver={(e) => {
            e.target.style.borderBottom = '3px solid #ff004f';
          }}
          onMouseOut={(e) => {
            e.target.style.borderBottom = '3px solid transparent';
          }}
        >
          Exercises
        </a>
      </Stack>
      {/* Responsive Dropdown */}
      <IconButton
        onClick={isDrawerOpen ? closeDrawer : openDrawer}
        sx={{ display: { sm: 'none', xs: 'block' }, ml: 'auto' }}
      >
        {isDrawerOpen ? <CloseIcon /> : <MenuIcon />}
      </IconButton>
      <Drawer
        anchor="right"
        open={isDrawerOpen}
        sx={{ display: { sm: 'none', xs: 'block' } }}
      >
        {/* List of Links */}
        <List>
          <ListItem>
            <Link to="/" style={{ textDecoration: 'none', color: '#3A1212' }} onClick={closeDrawer}>
              Home
            </Link>
          </ListItem>
          <ListItem>
            <a href="#exercises" style={{ textDecoration: 'none', color: '#3A1212' }} onClick={closeDrawer}>
              Exercises
            </a>
          </ListItem>
          {/* Add more links as needed */}
        </List>
      </Drawer>
    </Stack>
  );
};

export default Navbar;
